.step1 :not(label) {
    text-align: center;
}
.step1 label {
    font-weight: 500;
}
input[type='email'],
input[type='password'],
input[type='text'] {
    text-align: left;
}

@media only screen and (max-width: 600px) {
    .additionally-desc {
        display: none;
    }
    .step1 {
        margin: 0px;
        padding: 20px;
    }
    /* .step1-container {
        height: auto !important;
    } */
}
@media only screen and (min-width: 600px) {
    .step1 {
        margin: 80px;
    }
    /* .additionally-desc-content {
        margin: 20px;
    } */
    .step1-container {
        height: 100vh;
        padding: 80px;
    }
}
